            pThis.addPatients.attack_time=pThis.dateTime(pThis.addPatients.attack_time,19)
          }
          if(pThis.addPatients.help_date){
            pThis.addPatients.help_date=pThis.dateTime(pThis.addPatients.help_date,19)
