    // 身份证规则
    identifyRule(ev,json,num){
        var pThis=this;
        if(ev.target.value>0){
          var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
          if(reg.test(ev.target.value) === false){
            pThis.$toast.center("warn.png","身份证输入不合法");
            return false;
          }
        }
    },
    //数字验证
    numRule(ev,json,num){
      if (ev.keyCode == 37 || ev.keyCode == 39) {
        return;
      }else {
        if(!num){
          num=3
        }
        ev.target.value = this.validate.formatTwo(/[^\d]/g, ev);
        if(ev.target.value=='undefined'){
          ev.target.value="";
        }
        this[json[0]][json[1]] = ev.target.value;
      }
    },
