          identifyid:"",//身份证号
          birthday:"",//出生日期
          contact_name:"",//联系人
          age_value:"",//年龄---必填
          age_unit:"",//年龄单位
          contact_phone:"",//联系人电话
          attack_address:"",//发病地址---选填
          attack_time:"",//发病时间---必填
          action_in_chief:"",//主诉
          is_help:'1',//是否呼救  选填（1 是 0否,传1，0或空值）
          help_date:"",//呼救时间
          is_persistent:0,//持续性胸闷/胸痛  选填（1 是 0否,传1，0或空值）
          is_intermittent:0,//间断性胸闷/胸痛  选填（1 是 0否,传1，0或空值）
          is_laxation:0,//胸痛症状已缓解  选填（1 是 0否,传1，0或空值）
          is_bellyache:0,//腹痛  选填（1 是 0否,传1，0或空值）
          is_dyspnea:0,//呼吸困难  选填（1 是 0否,传1，0或空值）
          is_shock:0,//休克  选填（1 是 0否,传1，0或空值）
          is_heart_attack:0,//心衰  选填（1 是 0否,传1，0或空值）
          is_malignant_arrhythmia:0,//恶性心律失常  选填（1 是 0否,传1，0或空值）
          is_cpr:0,//心肺复苏  选填（1 是 0否,传1，0或空值）
          is_hemorrhage:0,//合并出血  选填（1 是 0否,传1，0或空值）
          is_other:0,//其它  选填（1 是 0否,传1，0或空值）
          car_code:"",//车辆
          follow_car_doctor:"",//随车医生
          follow_car_nurse:"",//随车护士
          driver:"",//驾驶员
          hand_ring_code:"",//手环编号
          box_code:"",//实时监控盒子编号
      };
    },
    //发送定位
    carLocation(){
      let _this = this;
      console.log(_this.locationCarId);
      this.location.thisLocation(_this.locationCarId);
    },
  }
};
</script>
<style lang="scss" scoped>
  @media screen and (max-width:1000px){
    .ItemStyle>li{margin-bottom:6px;}
  }
  @media screen and (min-width:1001px){
    .ItemStyle>li{margin-bottom:10px;}
  }
  .clearfix:after{clear:both;display:block;content:"";}
  .disabledStyle{background:rgb(221,221,221) !important;color:#fff !important;border:1px solid rgb(221,221,221) !important;}
  .button{margin-left:10px;height:34px;line-height:34px;width:110px;font-size:14px;padding:0;}
  .home {
    background:#f2f6ff;
    .mainContent {
      font-family:"微软雅黑";
      .taskStatus{
        margin-left:22px;position:relative;height:66px;
        .statusTitle{float:left;display:inline-block;font-size:16px;font-weight:bold;color:#333;height:56px;line-height:56px;margin-right:14px;}
        .taskStatusUl{
          float:left;height:34px;line-height:34px;margin-top:10px;background:#e6ebf8;border-radius:20px;
          li{ cursor:pointer;float:left;height:34px;line-height:34px;font-size:14px;width:82px;padding:0 16px;text-align:center;color:#333;}
          .active{background:#43c479;border-radius:20px;color:#fff;}
        }
        .addNewTask{display:inline-block;vertical-align:middle;position:absolute;right:10px;top:10px;}
      }
      .patientInfo{
        .patientItem{
          position:relative;font-size:14px;height:150px;box-shadow: 0 0 1px #dee5f5;
          background:#fff;padding:10px 0 6px 22px;margin-bottom:10px;
          h3{
            font-weight:bold;font-size:14px;margin-bottom:10px;
            span{margin-right:28px;color:#173f99;}
          }
          p{
            margin-bottom:6px;vertical-align: middle;
            span{color:#333;}
          }
          span{
            color:#999;font-size:14px;
          }
          .btnGroup{
            position:absolute;right:10px;top:30px;
          }
          .btnStyle{
            padding:6px 24px;background:#fff;color:#4c77d4;font-size:14px;
          }
        }
      }
      .newBtnTask{
        height:150px;line-height:150px;background:#f9fbff;box-shadow: 0 0 1px #dee5f5;
        span{
          color:#6b8dda;font-size:16px;
        }
      }
    }
  }
  .checkboxStyle{
    display:inline-block;width:676px;vertical-align: top;text-align:left;position:relative;padding-left:4px;
    label{display:inline-block;width:160px;float:left;height:24px;line-height:24px;}
  }
.formStyle {
  font-size: 18px;
  // overflow: hidden;
  overflow-x: hidden;
  overflow-y: auto;
  /*margin-top: 10px;*/
  /*margin-top: 20px;*/
  .ItemStyle {
    position: relative;
    margin-top: 20px;
    height: 300px;
    >li {
      width: 266px;
      float: left;
      /*margin-bottom: 10px;*/
      line-height: 20px;
      .w120 {
        width: 180px !important;
      }
    }
  }
}
  .checkboxGroupStyle{
    label{
      vertical-align: middle;width:126px;display:inline-block;
      span{
        display:inline-block;
        input{vertical-align: middle;display:inline-block;}
      }
    }
  }
</style>



